.boardlist{
    width: 1000px;
    margin: 100px auto;
    padding: 50px 0;
    border: 1px solid #ff6831;
    position: relative;
}
.boardlist ul{
    display: flex;
    justify-content:center;
    margin-top: 30px;
}
.boardlist ul li{
    width: 200px;
    font-weight: bold;
    text-align: left;
    padding-left: 20px;
}
.boardlist ul li+li{
    border-left: 1px solid #ff6831
}
.boardlist ul .title{
    width: 600px;
}
/* 작성하기 버튼 */
button.write{
    position: absolute;
    right: 0;
    bottom: -100px;
    width: 75px;
    height: 40px;
    color: #fff;
    background: #ff6831;
    border-radius: 20px;
    font-weight: bold;
}
.write:hover{
    background: #ca3600;
}

/* --- */
.look ul .title{
    cursor: pointer;
}
.look ul .title:hover{
    text-decoration:underline;
}
