.boardwrite{
    width: 1000px;
    margin: 100px auto;
    padding: 50px 0;
    border: 1px solid #ff6831;
}
.write_block{
    text-align: left;
    padding: 30px 200px 0;
}
.write_block h3{
    margin-bottom: 10px;
}
.write_block input,
.write_block textarea{
    width: 100%;
    margin-bottom: 30px;
    background: none;
    border: none;
    background: #666;
    color: #fff;
}
.write_block input{
    height: 30px;
    text-indent: 10px;
}
.write_block input::placeholder{
    color: #fff;
}
.write_block input:focus,
.write_block textarea:focus{
    outline: none;
    border-bottom: 2px solid #ff6831;
    background: none;
    color: #fff;
}
/* 저장/리셋 버튼 */
.btn_wrap{
    display: flex;
    justify-content: center;
    height: 40px;
    gap: 20px;
}
.btn_wrap button{
    width: 75px;
    height: 40px;
    color: #fff;
    font-weight: bold;
    border-radius: 20px;
}
.btn_wrap .save{
    background: #ff6831;
}
.btn_wrap .save:hover{
    background: #ca3600;
}
.btn_wrap .reset{
    background: white;
    color: #666;
}
.btn_wrap .reset:hover{
    color: #fff;
    background: #999;
}