.boardview{
    width: 1000px;
    margin: 100px auto;
    padding: 50px 0 0;
    border: 1px solid #ff6831;
}
/* 수정하기,삭제하기 버튼 */
.modi_wrap{
    display: flex;
    justify-content: end;
    height: 40px;
    gap: 20px;
    margin: 20px;
}
.modi_wrap button{
    width: 75px;
    height: 40px;
    color: #fff;
    font-weight: bold;
    border-radius: 20px;
    background: #ff6831;
}
.modi_wrap button:hover{
    background: #ca3600;
}
.modi_wrap .delete{
    background: white;
    color: #666;
}
.modi_wrap .delete:hover{
    background: #666;
    color: #fff;
}

/* 내용박스 */
.infom_box{
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    /* border: 1px solid #fff; */
}
.infom_box .info{
    text-align: left;
}
.infom_box .info .title{
    height: 60px;
    line-height: 60px;
    /* margin: 20px; */
    border-bottom: 1px solid #fff;
}
.infom_box .info .texts{
    height: 180px;
    line-height: 60px;
    /* margin: 20px; */
}
.number_box p{
    width: 100%;
    margin: 20px;
    text-align: left;
}